html, body {
  color: #242424;
  line-height: 1.6em;
  font-family: "Lato", sans-serif; }

.cm-button {
  background-color: #00A6ED;
  border: 2px solid #00A6ED;
  color: white;
  padding: 17px 10px;
  border-radius: 35px;
  min-width: 200px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "Varela Round", sans-serif;
  transition: background-color .3s, border-color .3s; }
  .cm-button:hover {
    background-color: #001835;
    border-color: #001835;
    cursor: pointer; }

h2 {
  font-size: 3em;
  font-family: "Varela Round", sans-serif;
  font-weight: 400;
  line-height: 1.2; }

h3 {
  font-family: "Varela Round", sans-serif;
  font-size: 1.25em;
  font-style: italic;
  font-weight: 400;
  color: #00A6ED;
  line-height: 1.2; }

h4 {
  font-family: "Varela Round", sans-serif;
  font-size: 1.25em;
  font-weight: 400;
  line-height: 1.2; }

.scroll-to-top {
  position: fixed;
  right: 1em;
  bottom: 1em;
  color: #F6511D;
  font-size: 3em; }
  .scroll-to-top:hover {
    cursor: pointer; }

.responsive {
  display: block;
  width: 100%;
  height: auto; }

.cm-container {
  width: 1170px;
  margin: 0 auto;
  padding: 5.625em 0; }

.banner {
  background-color: #F6511D;
  height: 38.5em;
  display: flex;
  align-items: center; }
  .banner .cm-container {
    display: flex;
    align-items: center;
    padding: 0; }
    .banner .cm-container .banner-image {
      width: 50%; }
    .banner .cm-container .call-to-action {
      width: 50%; }
      .banner .cm-container .call-to-action h2 {
        color: white;
        margin-bottom: 30px; }
      .banner .cm-container .call-to-action p {
        color: white;
        margin-bottom: 30px; }
      .banner .cm-container .call-to-action .cm-button:first-of-type {
        margin-right: 30px; }

.contact .flex {
  display: flex; }
  .contact .flex .cm-info, .contact .flex .contact-form {
    width: 50%;
    margin-top: 3.75em; }
  .contact .flex .cm-info .info-item {
    display: flex;
    margin-bottom: 2em;
    align-items: center; }
    .contact .flex .cm-info .info-item .cm-icon {
      margin-right: 1.2em;
      font-size: 1.5em; }
      .contact .flex .cm-info .info-item .cm-icon svg path {
        fill: #00A6ED; }
  .contact .flex .cm-info .social-networks .social-icons {
    display: flex; }
    .contact .flex .cm-info .social-networks .social-icons .icon {
      padding: 10px;
      background-color: #F6511D;
      margin-right: 1em;
      border-radius: 50%;
      transition: background-color .3s;
      min-width: 45px;
      text-align: center;
      margin-top: 1em; }
      .contact .flex .cm-info .social-networks .social-icons .icon svg path {
        fill: white; }
      .contact .flex .cm-info .social-networks .social-icons .icon:hover {
        background-color: #001835; }

.about {
  background-color: #f5f7fb; }
  .about .cm-container {
    display: flex; }
    .about .cm-container .content {
      width: 50%; }
    .about .cm-container .image {
      width: 50%; }

.services h2, .services h3 {
  text-align: center; }

.services .services-items {
  display: flex; }
  .services .services-items .items {
    flex: 1;
    border: 3px solid #d1dceb;
    border-radius: 18px;
    margin-right: 30px;
    padding: 60px 30px;
    margin-top: 50px;
    text-align: center; }
    .services .services-items .items:last-of-type {
      margin-right: 0; }
    .services .services-items .items .icon {
      width: 30%;
      margin: 0 auto; }
    .services .services-items .items h4 {
      font-weight: 400;
      font-style: normal;
      font-size: 30px;
      line-height: 1.2;
      letter-spacing: 0;
      margin-top: 30px; }
      .services .services-items .items h4:after {
        content: '';
        display: block;
        width: 50px;
        height: 5px;
        background: #23bc3d;
        text-align: center;
        margin: 30px auto 0 auto; }
    .services .services-items .items p {
      margin-top: 30px; }

.outsourcing {
  background-color: #00A6ED; }
  .outsourcing .cm-container {
    display: flex; }
    .outsourcing .cm-container .image {
      flex: 1; }
    .outsourcing .cm-container .content {
      width: 50%; }
      .outsourcing .cm-container .content h2, .outsourcing .cm-container .content h3, .outsourcing .cm-container .content p {
        color: white;
        text-align: right; }

header {
  padding: 15px 0; }
  header nav.cm-container {
    padding: 0;
    display: flex; }
    header nav.cm-container .logo {
      width: 150px; }
    header nav.cm-container .nav-links {
      flex: 1; }
      header nav.cm-container .nav-links ul {
        list-style: none;
        display: flex;
        justify-content: flex-end; }
        header nav.cm-container .nav-links ul li {
          display: inline-block; }
          header nav.cm-container .nav-links ul li a {
            height: 50px;
            display: block;
            text-decoration: none;
            color: #242424;
            margin-right: 24px;
            line-height: 50px;
            transition: color .3s; }
            header nav.cm-container .nav-links ul li a:hover {
              color: #F6511D; }
          header nav.cm-container .nav-links ul li:last-of-type a {
            margin-right: 0; }

footer {
  background-color: #242424;
  text-align: center;
  padding-top: 30px; }
  footer .logo {
    margin: 0 auto; }
  footer p {
    color: white;
    padding: 30px 0; }
  footer .cm-container {
    padding: 0; }

.cm-form-group {
  width: 100%;
  margin-bottom: 1.25em; }
  .cm-form-group .cm-form-control {
    width: 100%;
    border: 2px solid #e3eaf3;
    padding: 17px 25px;
    line-height: 22px;
    border-radius: 35px; }
    .cm-form-group .cm-form-control.error {
      border-color: #e3342f; }
  .cm-form-group textarea {
    height: 8.75em; }
  .cm-form-group button {
    background-color: #F6511D;
    border: 2px solid #F6511D;
    color: white;
    padding: 17px 10px;
    border-radius: 35px;
    min-width: 200px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: "Varela Round", sans-serif;
    transition: background-color .3s, border-color .3s;
    margin-right: 30px; }
    .cm-form-group button:hover {
      background-color: #001835;
      border-color: #001835;
      cursor: pointer; }
  .cm-form-group #responseMessage {
    display: none;
    flex: 1;
    line-height: 56px;
    text-align: center;
    border: 2px solid #00A6ED;
    border-radius: 36px; }
